export default {
  name: 'OnlineStatusSelect',
  props: {
    checked: {
      type: Boolean,
      default: false
    },
    icon: {
      type: String,
      required: true
    },
    type: {
      type: String,
      required: true
    },
    label: {
      type: String,
      required: true
    },
    subline: {
      type: String,
      default: null
    }
  },
  computed: {
    id: function id() {
      return "user-status-online-status-".concat(this.type);
    }
  },
  methods: {
    onChange: function onChange() {
      this.$emit('select', this.type);
    }
  }
};